* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  font-family: Montserrat, sans-serif;
}

a {
  text-decoration: none;
}

.products {
  flex-wrap: wrap;
  justify-content: space-between;
  margin-left: 80px;
  display: flex;
}

.navegation {
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  margin-left: 6%;
  margin-right: 6%;
  padding-top: 2.5%;
  padding-bottom: 2.5%;
  display: flex;
}

.navegation__text {
  width: 100%;
  margin-left: 20%;
  margin-right: 10%;
  padding-top: 1.8%;
}

.navegation__pag {
  color: #168282;
  padding-left: 5%;
  padding-right: 5%;
  font-size: medium;
  font-weight: 500;
}

.navegation__here {
  color: #168282;
  padding-left: 5%;
  padding-right: 5%;
  font-weight: 700;
}

.navegation__block {
  width: 100%;
  z-index: -1;
  background: #fff;
  box-shadow: -11px 4px 9px #00000026;
}

.navegation__icon {
  width: 20%;
}

.title {
  margin-top: 6%;
  margin-left: 6%;
}

.allproducts {
  flex-direction: row;
  margin-top: 4%;
  margin-left: 6%;
  margin-right: 10%;
  display: flex;
}

h2 {
  color: #137070;
}

.line {
  width: 90%;
  background-color: #137070;
  border: .5px solid #137070;
  border-radius: 2px;
  margin-top: 1%;
  margin-bottom: 3%;
}

.product__info {
  width: 271px;
  height: 363px;
  border: 1px solid #007a7a;
  border-radius: 25px;
  flex-direction: column;
  align-items: center;
  margin-bottom: 10%;
  margin-right: 5%;
  padding-top: 5%;
  display: flex;
}

.product__name {
  color: #646464;
  padding-top: 5%;
  font-size: larger;
  font-weight: 600;
}

.material {
  color: #646464;
  font-size: medium;
  font-weight: 400;
}

.price {
  color: #646464;
  margin-top: 3%;
  font-size: larger;
  font-weight: 600;
}

.filters {
  width: 280px;
  flex-direction: column;
  display: flex;
}

.filters_text {
  color: #646464;
  font-size: larger;
}

.filtes__material, .filters__price {
  margin-top: 15%;
}

input {
  border: none;
  text-decoration: none;
}

.form {
  width: 270px;
  height: 49px;
  color: #414040;
  border: 1px solid #137070;
  border-radius: 14px;
  margin-top: .7%;
  padding-left: 2%;
}

.footer {
  background-color: #137070;
  margin-top: 3%;
  padding-top: 3%;
  padding-bottom: 3%;
  padding-left: 6%;
}

.footer__contain {
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  display: flex;
}

.footer__text {
  color: #fff;
  padding-top: 1%;
  padding-left: 30%;
  font-size: small;
}

.lds-ellipsis {
  width: 80px;
  height: 80px;
  display: inline-block;
  position: relative;
}

.lds-ellipsis div {
  width: 13px;
  height: 13px;
  background: #007a7a;
  border-radius: 50%;
  animation-timing-function: cubic-bezier(0, 1, 1, 0);
  position: absolute;
  top: 33px;
}

.lds-ellipsis div:nth-child(1) {
  animation: lds-ellipsis1 .6s infinite;
  left: 8px;
}

.lds-ellipsis div:nth-child(2) {
  animation: lds-ellipsis2 .6s infinite;
  left: 8px;
}

.lds-ellipsis div:nth-child(3) {
  animation: lds-ellipsis2 .6s infinite;
  left: 32px;
}

.lds-ellipsis div:nth-child(4) {
  animation: lds-ellipsis3 .6s infinite;
  left: 56px;
}

@media only screen and (max-width: 420px) {
  .allproducts {
    flex-direction: column;
    margin-top: 4%;
    margin-left: 6%;
    margin-right: 10%;
    display: flex;
  }

  .title {
    margin-top: 20%;
    margin-left: 6%;
  }

  .filters {
    width: 100%;
    margin-top: 10%;
  }

  .filtes__material {
    margin-top: 5%;
  }

  .form {
    width: 100%;
    height: 49px;
    color: #414040;
    border: 1px solid #137070;
    border-radius: 14px;
    margin-top: .7%;
    padding-left: 2%;
  }

  .products {
    flex-wrap: column;
    justify-content: space-between;
    margin-top: 6%;
    margin-left: 10%;
    display: flex;
  }

  .footer {
    background-color: #137070;
    margin-top: 10%;
    padding: 15% 6%;
  }

  .navegation__text {
    display: none;
  }

  .navegation__icon {
    width: 100%;
    flex-direction: row;
    padding-left: 40%;
    display: flex;
  }
}

@keyframes lds-ellipsis1 {
  0% {
    transform: scale(0);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes lds-ellipsis3 {
  0% {
    transform: scale(1);
  }

  100% {
    transform: scale(0);
  }
}

@keyframes lds-ellipsis2 {
  0% {
    transform: translate(0);
  }

  100% {
    transform: translate(24px);
  }
}

